// src/pages/home/PasswordGateSimples.tsx
import React, { Children, ReactNode, useState } from 'react';
import { useNavigate } from 'react-router-dom';

type PasswordGateSimplesProps = {
  children?: ReactNode;
};

const PasswordGateSimples: React.FC <PasswordGateSimplesProps> = ({children}) => {
  const navigate = useNavigate();
  const [input, setInput] = useState('');
  const [error, setError] = useState('');

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (input === 'suP@25') {
      navigate('/home');
    } else {
      setError('Incorrect Password');
    }
  };

  return (
    <div style={{
      height: '100vh',
      display: 'flex',
      justifyContent: 'center',
      alignItems: 'center',
      background: '#f2f2f2'
    }}>
      <form onSubmit={handleSubmit} style={{
        background: 'white',
        padding: 30,
        borderRadius: 10,
        boxShadow: '0 0 10px rgba(0,0,0,0.1)'
      }}>
        <h2>Your Password</h2>
        <input
          type="password"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Senha"
          style={{
            width: '100%',
            padding: 10,
            marginTop: 10,
            marginBottom: 10
          }}
        />
        <button type="submit" style={{
          width: '100%',
          padding: 10,
          background: '#007bff',
          color: 'white',
          border: 'none',
          borderRadius: 5
        }}>Submit</button>
        {error && <p style={{ color: 'red', marginTop: 10 }}>{error}</p>}
      </form>
      {children}
    </div>
  );
};

export default PasswordGateSimples;